import React from "react"
import { Card, CardBody, Row, Col, Input, Button, } from "reactstrap"
import { AgGridReact } from "ag-grid-react"
import { ContextLayout } from "../../utility/context/Layout"
// import { ChevronDown } from "react-feather"
import axios from "axios";
import {connect} from "react-redux";
import Customize from "./CustomizeDeals"
import Chart from "react-apexcharts"

import Breadcrumbs from "../../components/@vuexy/breadCrumbs/BreadCrumb"

import "../../assets/scss/plugins/tables/_agGridStyleOverride.scss"
import "../../assets/scss/plugins/tables/_customAgGridStyleOverride.scss"
import "../../assets/scss/pages/app-email.scss"

class Finance extends React.Component {
    state = {
        rowData: null,
        pinnedTopRowData: [],
        pinnedBottomRowData: [],
        paginationPageSize: 20,
        currenPageSize: "",
        getPageSize: "",
        defaultColDef: {},
        columnDefs: [
            {
                headerName: "Finance Person",
                field: "finance_person",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Sales",
                field: "sales",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Warranty Total & Pen%",
                field: "warranty_total_pen",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Warranty Gross",
                field: "warranty_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Gap Total & Pen%",
                field: "gap_total_pen",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Gap Gross",
                field: "gap_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Miscfinance Total & Pen%",
                field: "miscfinance_tatal_pen",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Miscfinance Gross",
                field: "miscfinance_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Reserve",
                field: "reserve",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Back End",
                field: "back_end",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "FP%",
                field: "fp",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
        ],
        composeMailStatus: false,
        options: {
            colors: this.props.themeColors,
            plotOptions: {
                bar: {
                    horizontal: true
                }
            },
            dataLabels: {
                enabled: false
            },
            xaxis: {
                categories: [
                    "Memo",
                    "Jake R",
                    "Dan",
                    "House",
                    "Tyler",
                ],
                tickAmount: 5
            }
        },
        series: [
            {
              data: [18.0, 24.0, 1.0, 2.0, 1.0]
            }
        ]
    }

    componentDidMount() {
        axios.get("/api/aggrid/data").then(response => {
        // let rowData = response.data.data // replace for product
        let tempdata = {
            finance_person: "Jake J",
            sales: "14",
            warranty_total_pen: "(6)25%",
            warranty_gross: "$3,532.00",
            gap_total_pen: "(11)61.1%",
            gap_gross: "$42,031.00",
            miscfinance_tatal_pen: "(5)20.8%",
            miscfinance_gross: "$3,984.00",
            reserve: "$12,528.00",
            back_end: "$32,988.00",
            fp: "75%"
        };

        let colWidth = window.innerWidth;
        let columnDefs = this.state.columnDefs
        for (let i = 0; i < this.state.columnDefs.length; i++) {
            columnDefs[i].width = (colWidth-(colWidth/5)) / this.state.columnDefs.length;
            columnDefs[i].minWidth = 120;
        }

        let rowData = [];
        for (let i = 0; i < 20; i++) {
            rowData.push(Object.assign({}, tempdata));
        }

        JSON.stringify(rowData)
            this.setState({
                rowData,
                columnDefs
            });
        })
    }

    UNSAFE_componentWillMount () {
        this.initTable(this.props);
    }

    UNSAFE_componentWillReceiveProps (nextprops) {
        this.initTable(nextprops);
    }

    initTable(props) {
        let pinnedBottomRowData = [];
        pinnedBottomRowData.push(
            {
                finance_person: "Total",
                sales: "46",
                warranty_total_pen: "(13)28.3%",
                warranty_gross: "$22,689.00",
                gap_total_pen: "(16)42.1%",
                gap_gross: "$7,355.00",
                miscfinance_tatal_pen: "(12)26.1%",
                miscfinance_gross: "$10,633.00",
                reserve: "$23,805.00",
                back_end: "$64,482.00",
            }
        );
        pinnedBottomRowData.push(
            {
                finance_person: "Average",
                warranty_gross: "$1,745.31",
                gap_gross: "$459.69",
                miscfinance_gross: "$886.08",
                reserve: "$610.38",
                back_end: "$1,401.78",
            }
        );

        this.setState({
            pinnedBottomRowData,
            composeMailStatus: false
        });
    }

  onGridReady = params => {
    this.gridApi = params.api
    this.gridColumnApi = params.columnApi
    this.setState({
      currenPageSize: this.gridApi.paginationGetCurrentPage() + 1,
      getPageSize: this.gridApi.paginationGetPageSize(),
      totalPages: this.gridApi.paginationGetTotalPages()
    })
  }

  updateSearchQuery = val => {
    this.gridApi.setQuickFilter(val)
  }

  filterSize = val => {
    if (this.gridApi) {
      this.gridApi.paginationSetPageSize(Number(val))
      this.setState({
        currenPageSize: val,
        getPageSize: val
      })
    }
  }
  handleCustomize() {
    this.handleComposeSidebar("open")
    this.onSetSidebarOpen(false)
  }
  handleComposeSidebar = status => {
    if (status === "open") {
      this.setState({
        composeMailStatus: true
      })
    } else {
      this.setState({
        composeMailStatus: false
      })
    }
  }

  onSetSidebarOpen = open => {
    this.setState({ sidebarOpen: open })
  }
  handleMainAndComposeSidebar = () => {
    this.handleComposeSidebar("close")
    this.onSetSidebarOpen(false)
  }

  render() {
    const { rowData, defaultColDef, pinnedBottomRowData } = this.state

    return (
    <React.Fragment>
        <Breadcrumbs breadCrumbTitle="Finance" breadCrumbParent="Reports" breadCrumbActive="Finance"/>
        <Card onClick={this.handleMainAndComposeSidebar}>
            <CardBody>
                <div className="d-flex flex-wrap justify-content-end align-items-center">
                    <div className="mb-1"></div>
                    <div className="d-flex flex-wrap justify-content-between mb-1">
                        <div className="table-input mr-1">
                            <Input placeholder="search..." onChange={e => this.updateSearchQuery(e.target.value)} value={this.state.value} />
                        </div>
                        <div className="customize-btn">
                            <Button.Ripple block color="primary" onClick={() => this.handleCustomize()} >
                                Customize
                            </Button.Ripple>
                        </div>
                        <div className="export-btn">
                            <Button.Ripple color="primary" onClick={() => this.gridApi.exportDataAsCsv()} >
                                Export as CSV
                            </Button.Ripple>
                        </div>
                    </div>
                </div>
                <div style={{padding: "10px 20px"}}>
                    <Row>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Deals:</Col>
                                <Col xs="6">46</Col>
                            </Row>
                        </Col>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Back End Gross:</Col>
                                <Col xs="6">$108,754.00</Col>
                            </Row>
                        </Col>
                        <Col sm="3"></Col>
                        <Col sm="3"></Col>
                    </Row><br/>
                    <Row>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Unfunded Deals:</Col>
                                <Col xs="6">24</Col>
                            </Row>
                        </Col>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Unfunded Amount:</Col>
                                <Col xs="6">$108,754.00</Col>
                            </Row>
                        </Col>
                        <Col sm="3"></Col>
                        <Col sm="3"></Col>
                    </Row><br/>
                    <Row>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Warranties Sold:</Col>
                                <Col xs="6">13</Col>
                            </Row>
                        </Col>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Warranty Gross:</Col>
                                <Col xs="6">$108,754.00</Col>
                            </Row>
                        </Col>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Warranty Penetration:</Col>
                                <Col xs="6">(13)28.3%</Col>
                            </Row>
                        </Col>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Warranty Average:</Col>
                                <Col xs="6">$1,745.31</Col>
                            </Row>
                        </Col>
                    </Row><br/>
                    <Row>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Gap Plan Sold:</Col>
                                <Col xs="6">16</Col>
                            </Row>
                        </Col>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Gap Gross:</Col>
                                <Col xs="6">$108,754.00</Col>
                            </Row>
                        </Col>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Gap Penetration</Col>
                                <Col xs="6">(16)42.1%</Col>
                            </Row>
                        </Col>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Gap Average:</Col>
                                <Col xs="6">$459.69</Col>
                            </Row>
                        </Col>
                    </Row><br/>
                    <Row>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Miscfin Sold:</Col>
                                <Col xs="6">12</Col>
                            </Row>
                        </Col>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Miscfin Gross:</Col>
                                <Col xs="6">$108,754.00</Col>
                            </Row>
                        </Col>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Miscfin Penetration:</Col>
                                <Col xs="6">(12)26.1%</Col>
                            </Row>
                        </Col>
                        <Col sm="3" xs="12">
                            <Row>
                                <Col xs="6">Miscfin Average:</Col>
                                <Col xs="6">$886.08</Col>
                            </Row>
                        </Col>
                    </Row>
                </div>
                <Chart options={this.state.options} series={this.state.series} type="bar" height={350} />
                <div className="d-flex flex-wrap mb-1">
                    <div className="mr-1">
                        <Button.Ripple block color="primary">
                            Total Deals
                        </Button.Ripple>
                    </div>
                    <div className="mr-1">
                        <Button.Ripple block color="primary">
                            Total Back End
                        </Button.Ripple>
                    </div>
                    <div className="mr-1">
                        <Button.Ripple block color="primary">
                            Average Back End
                        </Button.Ripple>
                    </div>
                </div>
            </CardBody>
        </Card>
        <div className="email-application">
            <Customize handleComposeSidebar={this.handleComposeSidebar} currentStatus={this.state.composeMailStatus}/>
            <Card className="agGrid-card" style={{height: "100% !important", paddingBottom: "2%"}} onClick={this.handleMainAndComposeSidebar}>
                <CardBody className="py-0" style={{height: "100% !important"}}>
                {
                this.state.rowData === null ? null : (
                    <div className="ag-theme-material w-100 my-2 ag-grid-table" style={{height: "100% !important", width: "100% !important"}}>
                        <ContextLayout.Consumer>
                            {context => (
                            <AgGridReact
                                gridOptions={{}}
                                rowSelection="multiple"
                                defaultColDef={defaultColDef}
                                columnDefs={this.state.columnDefs}
                                rowData={rowData}
                                onGridReady={this.onGridReady}
                                floatingFilter={true}
                                pagination={true}
                                enableRtl={context.state.direction === "rtl"}
                                
                                pinnedBottomRowData={pinnedBottomRowData}
                                rowHeight="40"
                            />
                            )}
                        </ContextLayout.Consumer>
                    </div>
                )}
                </CardBody>
            </Card>
        </div>
    </React.Fragment>
    )
  }
}

const mapStateToProps = state => {
    return {
        app: state.calender
    }
}

export default connect(mapStateToProps, {})(Finance);
